import { sysList } from '@/lib/axios/api';

export default {
  data () {
    return {
      listColumns: [
          {
              title: '序号',
              key: 'num',
              align: 'center',
              width:150
          },
          {
              title: '系统标识',
              key: 'sign',
              align: 'center'
          },
          {
              title: '系统URL',
              key: 'url',
              align: 'center'
          },
          {
              title: '备注',
              key: 'remarks',
              align: 'center'
          },
          {
              title: '操作',
              key: 'action',
              align: 'center',
              render: (h, params) => {
                  return h('div', [
                      h('Button', {
                          props: {
                              type: 'text',
                              size: 'small'
                          },
                          on: {
                              click: () => {
                                  this.toPath('/menu/'+params.row.sign)
                              }
                          }
                      }, '菜单管理'),
                      h('Button', {
                          props: {
                              type: 'text',
                              size: 'small'
                          },
                          on: {
                              click: () => {
                                  this.toPath('/user/'+params.row.sign)
                              }
                          }
                      }, '用户管理')
                  ]);
              }
          }
      ],
      listData: [
            {
                sign: 'John Brown',
                num: 18,
                url: 'New York No. 1 Lake Park',
                remarks: '2016-10-03'
            },
            {
                sign: 'Jim Green',
                num: 24,
                url: 'London No. 1 Lake Park',
                remarks: '2016-10-01'
            },
            {
                sign: 'Joe Black',
                num: 30,
                url: 'Sydney No. 1 Lake Park',
                remarks: '2016-10-02'
            },
            {
                sign: 'Jon Snow',
                num: 26,
                url: 'Ottawa No. 2 Lake Park',
                remarks: '2016-10-04'
            },
            {
                sign: 'Jon Snow',
                num: 26,
                url: 'Ottawa No. 2 Lake Park',
                remarks: '2016-10-04'
            },
            {
                sign: 'Jon Snow',
                num: 26,
                url: 'Ottawa No. 2 Lake Park',
                remarks: '2016-10-04'
            },
            {
                sign: 'Jon Snow',
                num: 26,
                url: 'Ottawa No. 2 Lake Park',
                remarks: '2016-10-04'
            },
            {
                sign: 'Jon Snow',
                num: 26,
                url: 'Ottawa No. 2 Lake Park',
                remarks: '2016-10-04'
            }
        ],
      getList: {                //获取当前账户下列表数据需传参数
         "PageIndex": 1,
         "PageSize": 10,
         "Sort": "ID",
         "SortDirection": 2
      },
    }
  },
  methods: {
    toPath (path,data) {
      this.$router.push({
        path:path
      })
    }
  },
  created: function(){
    // console.log('测试工具处',this.$publicMethod.chineseOnly("sdfsf@#@#"));
    sysList('/Home/SysList',this.getList)       // 获取全部菜单
    .then((res) => {
      console.log('成功',res);
      if((res.status === 200)&&res.data.Success){
        this.$local.save('Account',res.data.Data.Pager[0].Account);   // 将用户名存至本地 Storage(暂时没用)
        this.$emit('getAccount',res.data.Data.Pager[0].Account)       // 传递当前用户名至父级，父级至header页面处
      }else {
        this.toPath('/login')
      }
    })
    .catch((err,data) => {
      console.log('失败',err);
    })
  }
}



